



























































































































































































































































































































































































import Vue from "vue";
import { request } from "./client/client";
import "vue-json-pretty/lib/styles.css";
import VJsoneditor from "v-jsoneditor";

export default Vue.extend({
  name: "App",
  components: {
    VJsoneditor,
  },
  data: () => ({
    jsonViewOptions: {
      mode: "code",
    },
    networkError: false,
    drawer: null,
    responseTime: "",
    tab: null,
    filter: "",
    items: ["Inputs", "Headers"],
    displayVertical: true,
    historyDrawer: null,
    icon: "mdi-arrow-decision-outline",
    baseUrl: "http://localhost:4000",
    headers: {},
    inputs: {},
    model: 0,
    requests: [],
    selected: {},
    response: null,
    history: [],
    loading: false,
  }),
  mounted() {
    this.sync();
    window.addEventListener("keyup", this.enterListener);
  },
  beforeDestroy() {
    window.removeEventListener("keyup", this.enterListener);
  },
  methods: {
    sync() {
      this.requests = [];
      this.loading = true;
      this.networkError = false;
      request("wwActions", { baseURL: this.baseUrl })
        .then((r) => {
          this.requests = (r.data || []).filter(
            (action: any) => action.name !== "/wwActions"
          );
          if (!(this.selected as any).name && this.requests.length > 0) {
            this.selected = this.requests[0];
          }
        })
        .catch((r: any) => {
          if (r.message === "Network Error") {
            this.networkError = true;
          }
        })
        .finally(() => {
          setTimeout(() => (this.loading = false), 750);
        });
    },
    filterActions() {
      if (this.filter) {
        const filterLower = (this.filter || "").toLowerCase();
        return this.requests.filter((request: any) => {
          return request.name.toLowerCase().includes(filterLower);
        });
      }
      return this.requests;
    },
    execute() {
      const name = (this.selected as any).name;
      if (name) {
        this.response = null;
        this.responseTime = "";
        const data = this.inputs || {};
        const headers = this.headers || {};
        const config = { headers, data, baseURL: this.baseUrl };
        this.loading = true;
        this.networkError = false;
        const startTime = new Date().getTime();
        request(name, config)
          .then((r: any) => {
            this.responseTime = `${new Date().getTime() - startTime}ms`;
            this.response = r;
            this.addToHistory();
          })
          .catch((r: any) => {
            this.responseTime = `${new Date().getTime() - startTime}ms`;
            if (r.response) {
              this.response = r.response;
              this.addToHistory();
            } else if (r.request) {
              // The request was made but no response was received
              this.networkError = true;
            } else {
              // Something happened in setting up the request that triggered an Error
              this.networkError = true;
            }
          })
          .finally(() => {
            setTimeout(() => (this.loading = false), 750);
          });
      }
    },
    addToHistory() {
      const { selected, inputs, headers, response, responseTime } = this;
      const archivedRequest = {
        selected,
        inputs: inputs || {},
        headers: headers || {},
        response,
        responseTime,
      };
      (this.history as any).unshift({
        ...archivedRequest,
      });
    },
    restoreRequest(archivedRequest) {
      this.inputs = {};
      const {
        selected,
        inputs,
        headers,
        response,
        responseTime,
      } = archivedRequest;
      this.selected = selected;
      this.inputs = inputs;
      this.headers = headers;
      this.response = response as any;
      this.responseTime = responseTime;
    },
    select(request: any) {
      if (request !== this.selected) {
        this.response = null;
      }
      this.selected = request;
    },
    enterListener(e: KeyboardEvent) {
      e.preventDefault();
      e.stopImmediatePropagation();
      if (e.ctrlKey && e.keyCode == 13) {
        this.execute();
      }

      if (e.ctrlKey && e.keyCode == 66) {
        document.getElementById("find-action")!.focus();
      }
    },
    getStatusColor(status) {
      if (!status || typeof status !== "number") {
        return "gray";
      }
      if (status >= 200 && status < 300) {
        return "green darken-2";
      }
      if (status >= 300 && status < 400) {
        return "orange darken-2";
      }
      if (status >= 400) {
        return "red darken-2";
      }
      return "gray";
    },
    getKeysNumber(obj = {}) {
      if (!obj) {
        return 0;
      }
      return (Object.keys(obj) || []).length;
    },
  },
});
